body{
  height: 100vh;
  background: url("./assets//images/bg.jpg");
  background-size: 100% auto;
}

hr { 
  display: block; 
  height: 1px;
  border: 0; 
  border-top: 2px solid rgb(187, 187, 187) !important;
  margin: 1em 0; 
  padding: 0; 
}

.main-body{
  min-width: 1280px;
  overflow: auto
}

.login-container{
  padding-top: 100px
}

.login-form{
  background-color: rgba(255, 255, 255, 0.815);
  padding: 20px;
}

.login-form form{
  padding: 20px
}

.login-form form input {
  padding: 10px !important
}

.login-hospital-name{
  color: #33b5e5;
  text-align: center
}

.login-label{
  color: #33b5e5;
  padding-bottom: 10px !important;
}

.login-container img{
  width: 200px;
  display: block;
  margin: 10px auto;
}

/* navbar start */

.my-navbar{
  background-color: #0084acd7;
  height: 100px;
  color: rgb(250, 250, 250);
  box-shadow: 5px 5px 5px #777777d7;
  padding: 0px;
}

.navheader{
  display: inline-block;
  width: 500px;
  height: 100px
}

.my-navbar .logo{
  height: 80px;
  display: inline-block;
  /* margin-top: -30px; */
  margin-left: 20px;
  margin-right: 20px;
  
}

.my-navbar .profile{
  height: 40px;
  /* margin-bottom: 20px */
}

.search-input{
  width: calc(50% - 250px) !important;
}

.my-navbar h1 {
  font-weight: bold;
  margin-top: 26px !important;
  display: inline-block
}

.profile-dropdown{
  display: inline !important;
  float: right;
  margin: 23px;
  
}

.profile-dropdown .profile-name{
  font-size: 18px;
  background-color: #ffffff00 !important;
  box-shadow: none !important;
  outline: none !important;
  padding: 0 !important;
  color: rgb(236, 236, 236)
}

.profile-dropdown .profile-name:hover,.profile-dropdown .profile-name:focus{
  font-size: 18px;
  background-color: #ffffff00 !important;
  box-shadow: none;
}

/* navbar end */

/* Sidebar start */

.sidebar-icon{
  width: 30px;
  margin-top: -5px;
}

.flex-container{
  display: flex;
  width: 100%;
  height: calc(100vh - 100px);
}

.sidebar{
  height: calc(100vh - 100px);
  width: 300px;
  background-color: rgb(12, 25, 53);
  padding-top: 40px;
  overflow: auto;
}

.content{
  flex: 1;
  background-color: #ffffffb2;
  overflow: auto;
  padding: 10px;
}

.sidebar h3,.sidebar a{
  padding: 5px 10px !important;
  margin: 0 !important;
  color: rgb(179, 179, 179);
  font-weight: 400 !important;
  font-size: 20px;
}

.sidebar svg{
  width: 20px !important;
  margin-right: 12px;
}

.sidebar .secondary a{
  padding-left: 50px !important;
  font-size: 18px

}

.sidebar h3:hover, .sidebar a:hover{
  color: rgb(255, 255, 255);
}

.sidebar .active, .sidebar .active a{
  background-color: white;
  color: black !important
}


/* Sidebar end */

/* Home start */

.home-container{
  max-width: 800px;
  width: 80%;
  padding: 20px 20px;
  margin: 120px auto;
  background-color: white;
  box-shadow: 5px 5px 5px rgb(199, 199, 199)
}

.home-container .image-container{
  width: 200px;
  height: 200px;
  overflow: hidden;
  border-radius: 50%;
  border: 3px solid rgb(206, 206, 206);
  padding: 10px;
  background-color: #ffffffd7;
  display: block;
  margin: -100px auto;
}
.home-container .image-container img{
  width: 100%;
  height: 100%;
}

.home-container h3{
  text-align: center;
  margin-top: 140px;
  font-weight: 400;
  font-size: 40px;
  color: #2093bd;
  border-bottom: 2px solid #2093bd;
  margin-bottom: 30px;
  padding-bottom: 30px;
}

.time-container{
  text-align: center;
  margin: 50px;
}

.time-container span{
  font-size: 80px;
  color: #2093bd
}

.time-container h5{
  font-size: 25px;
  color: #2093bd;
  font-weight: 400
}

.time-container h6{
  font-size: 20px;
  color: #2093bd;
  font-weight: 400
}

/* Home end */

/* patient start */

.header{
  width: 100%;
  height: 50px;
  background-color: rgba(255, 255, 255, 0.24);
  padding: 5px
}

.my-btn{
  margin-top: 0px !important;
}

.table-container{
  width: 100%;
  overflow: auto;
  padding: 5px;
  background-color: rgba(255, 255, 255, 0.507)
}

.table-container table{
  width: 100%;
  max-width: 1200px;
  background-color: rgb(255, 255, 255);
  margin: 0 auto;
  border-collapse: collapse;
  border: 1px solid rgb(219, 219, 219);
  box-shadow: 0px 0px 2px gray
}

.table-container table th{
  background-color: rgb(241, 255, 255);
  padding: 5px;
  border: 1px solid rgb(219, 219, 219);
  font-size: 16px
}

.table-container table td{
  padding: 5px 10px;
  padding-left: 10px !important;
  border-bottom: 1px solid rgba(219, 219, 219, 0.555);
  font-size: 16px
}

.table-container table tr:nth-child(odd){
  background-color: #f6f6ffd7
}

.table-container table td:last-child svg{
  margin: auto 3px;
  text-align: center !important;
  cursor: pointer;
}

.table-container table td:last-child .delete-icon{
  color: red;
}
.table-container table td:last-child .edit-icon{
  color: green
}

.hoverable-tr:hover{
  background-color: #bbedff !important;
  cursor: pointer;
}
.header span{
  font-size: 28px
}

.patient-form-container{
  border: 3px solid gray;
  /* padding: 0 !important; */
  background-color: white;
  margin-bottom: 50px;
}

.by1{
  border-left: 1px solid gray;
  border-right: 1px solid gray
}

.bx1{
  border-top: 1px solid gray;
  border-bottom: 1px solid gray
}
.br-1{
  border-right: 1px solid gray !important;
}

.bb-1{
  border-bottom: 1px solid gray !important;

}

.userinput{
  width: 100% !important;
  padding: 2px 15px
}

.my-input-container{
  padding: 0 !important;
}

.labels{
  font-weight: bold;
  font-size: 12px !important;
  margin: 1px !important
}


.doh-logo{
  width: 60px;
  margin-top: 7px;
}

.patient-form-header{
  margin-top:1px
}
.patient-form-header h6,.patient-form-header h4{
  margin: 0 !important;
  font-weight: 400;
}

.reset{
  padding: 0 !important;
  margin: 0 !important;
}
.patient-title-container{
  border-top: 3px solid gray;
  border-bottom: 3px solid gray;
  text-align: center;
}

.maiden-label{
  height: 27px !important;
}

.residential-input{
  height: 100px;
}
.patient-title-container .sub-title{
  margin-top: 10px;
  font-weight: bold;
  font-size: 18px;
  font-style: italic
}

.patient-title-container .title{
  margin-top: 5px;
  font-weight: bold;
  font-size: 30px;
}

.patient-title-container .instructions{
  margin-top: 1px;
  margin-bottom: 5px;
  
}

.patient-information-form{
  font-weight: bold;
  background-color: rgb(204, 204, 204);
  border-bottom: 3px solid gray;
}

.patient-info-inputs{
  border: none;
  width: 100%;
  padding: 0;
  margin: 1px;

}

.residential.patient-info-inputs{
  height: 80px;
  max-height: 80px;
}

.fac-house-no.patient-info-inputs{
  height: 33px;
}
.grayed{
  background-color: rgb(204, 204, 204);

}

/* patient end */